  .lg-spinner-btn .horiz-spinner {
    display: none;
    top: 13px;
    left: calc((100% - 70px)/2); }
  .lg-spinner-btn.loading button {
    color: transparent; }
  .lg-spinner-btn.loading .horiz-spinner {
    display: block; }
  .lg-spinner-btn.complete button {
    background-color: #fff;
    color: #ff7277;
    border: 1px solid #ff7277; }



